                Это только начало вашего путешествия в мир искусственного интеллекта.
              </p>
              
              <div class="grid md:grid-cols-2 gap-6 mb-6">
                <div class="bg-white p-6 rounded-xl shadow-sm">
                  <h3 class="text-xl font-semibold text-gray-900 mb-3">Что дальше?</h3>
                  <ul class="space-y-2 text-gray-700">
                    <li>• Изучите конкретные алгоритмы ML</li>
