                <div class="bg-white p-6 rounded-xl shadow-sm">
                  <h3 class="text-xl font-semibold text-gray-900 mb-3">Рекомендуемые ресурсы</h3>
                  <ul class="space-y-2 text-gray-700">
                    <li>• Coursera: Machine Learning Course</li>
                    <li>• Kaggle: Практические соревнования</li>
                    <li>• TensorFlow: Официальная документация</li>
                    <li>• Scikit-learn: Библиотека для Python</li>
                  </ul>
                </div>
              </div>
              
              <div class="bg-blue-50 border-l-4 border-blue-400 p-6 rounded-r-lg">
                <h3 class="text-lg font-semibold text-blue-800 mb-2">Помните</h3>
                <p class="text-blue-700">
                  Машинное обучение — это мощный инструмент, который может решать сложные задачи и улучшать нашу жизнь. 
                  Но важно использовать его ответственно и понимать его ограничения. 
