        </section>
      </article>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted, onUnmounted } from 'vue';
import { aiExerciseService } from '@/services/aiExerciseService';

// Reactive data
const isSubmitting = ref(false);
const isRequestingHint = ref(false);
const userAnswer = ref('');
const showHints = ref(false);
const hintsUsed = ref(0);
const submissions = ref<any[]>([]);
const currentSection = ref(1);

// Sections for navigation
const sections = ref([
  { id: 'introduction', title: 'Введение' },
  { id: 'what-is-ml', title: 'Что такое ML' },
  { id: 'types-of-ml', title: 'Типы ML' },
  { id: 'applications', title: 'Применение' },
  { id: 'assignment', title: 'Задание' },
  { id: 'conclusion', title: 'Заключение' }
]);

const totalSections = computed(() => sections.value.length);

// Static hints
const hints = ref([
  'Начните с определения машинного обучения как способа обучения компьютеров на данных',
  'Рассмотрите три основных типа: supervised, unsupervised и reinforcement learning',
  'Приведите конкретные примеры: рекомендательные системы, распознавание изображений, прогнозирование',
  'Объясните, когда используется каждый тип обучения'
]);

// Computed properties
const latestSubmission = computed(() => {
  return submissions.value[submissions.value.length - 1] || null;
});

const attemptsLeft = computed(() => {
  const maxAttempts = 3;
  return maxAttempts - submissions.value.length;
});

const visibleHints = computed(() => {
  return hints.value.slice(0, hintsUsed.value);
});

// Methods
const requestHint = async () => {
  console.log('Requesting hint...', { hintsUsed: hintsUsed.value, hintsLength: hints.value.length });
  
  if (hintsUsed.value >= hints.value.length) {
    console.log('No more hints available');
    return;
  }
  
  isRequestingHint.value = true;
  
  try {
    console.log('Calling aiExerciseService.generateHint...');
    // Используем AI для генерации подсказки
    const aiHint = await aiExerciseService.generateHint(
      'Объясните принцип работы машинного обучения',
      'Опишите основные принципы и подходы в машинном обучении, включая типы обучения и примеры применения.',
      userAnswer.value
    );
    
    console.log('Received AI hint:', aiHint);
    
    // Добавляем AI-подсказку к существующим подсказкам
    hints.value.push(aiHint);
    
    hintsUsed.value++;
    showHints.value = true;
    
    console.log('Hint added successfully');
  } catch (error) {
    console.error('Failed to get hint:', error);
    // Fallback к статическим подсказкам
    hintsUsed.value++;
    showHints.value = true;
    console.log('Using fallback hint');
  } finally {
    isRequestingHint.value = false;
  }
};

const submitAnswer = async () => {
  console.log('Submitting answer...', { 
    answerLength: userAnswer.value.trim().length, 
    isSubmitting: isSubmitting.value,
    attemptsLeft: attemptsLeft.value 
  });
  
  if (!userAnswer.value.trim() || userAnswer.value.trim().length < 50 || isSubmitting.value) {
    console.log('Answer validation failed');
    return;
  }
  
  isSubmitting.value = true;
  
  try {
    console.log('Calling aiExerciseService.evaluateTextAnswer...');
    // Используем AI для проверки ответа
    const evaluation = await aiExerciseService.evaluateTextAnswer(userAnswer.value, {
      exerciseId: 'demo_lesson_1',
      exerciseTitle: 'Объясните принцип работы машинного обучения',
      exerciseDescription: 'Опишите основные принципы и подходы в машинном обучении, включая типы обучения и примеры применения.',
      expectedAnswer: 'Ответ должен включать определение машинного обучения, описание типов (с учителем, без учителя, с подкреплением), примеры применения и объяснение различий между подходами.',
      evaluationCriteria: [
        'Правильность и полнота ответа',
        'Структурированность изложения',
        'Примеры и детализация',
        'Понимание концепций'
      ]
    });

    console.log('Received evaluation:', evaluation);

    // Создаем объект submission
    const submission = {
      id: `submission_${Date.now()}`,
      exerciseId: 'demo_lesson_1',
      userId: 'demo_user',
      answer: userAnswer.value,
      isCorrect: evaluation.isCorrect,
      feedback: evaluation.feedback,
      score: evaluation.score,
      attemptNumber: submissions.value.length + 1,
      submittedAt: new Date().toISOString()
    };

    submissions.value.push(submission);
    
    // Показываем дополнительные детали оценки
    if (evaluation.suggestions.length > 0 || evaluation.strengths.length > 0 || evaluation.improvements.length > 0) {
      let detailedFeedback = evaluation.feedback;
      
      if (evaluation.strengths.length > 0) {
        detailedFeedback += `\n\nСильные стороны:\n${evaluation.strengths.map(s => `• ${s}`).join('\n')}`;
      }
      
      if (evaluation.improvements.length > 0) {
        detailedFeedback += `\n\nЧто можно улучшить:\n${evaluation.improvements.map(i => `• ${i}`).join('\n')}`;
      }
      
      if (evaluation.suggestions.length > 0) {
        detailedFeedback += `\n\nРекомендации:\n${evaluation.suggestions.map(s => `• ${s}`).join('\n')}`;
      }
      
      submission.feedback = detailedFeedback;
    }
    
  } catch (error) {
    console.error('Failed to submit answer:', error);
    
    // Fallback submission в случае ошибки
    const fallbackSubmission = {
      id: `submission_${Date.now()}`,
      exerciseId: 'demo_lesson_1',
      userId: 'demo_user',
      answer: userAnswer.value,
      isCorrect: false,
      feedback: 'Произошла ошибка при проверке ответа. Попробуйте еще раз.',
      score: 0,
      attemptNumber: submissions.value.length + 1,
      submittedAt: new Date().toISOString()
    };
    
    submissions.value.push(fallbackSubmission);
  } finally {
    isSubmitting.value = false;
  }
};

const formatText = () => {
  // Basic text formatting
  userAnswer.value = userAnswer.value
    .split('\n')
    .map(line => line.trim())
    .filter(line => line.length > 0)
    .join('\n\n');
};

const clearText = () => {
  userAnswer.value = '';
};

const startNewAttempt = () => {
  userAnswer.value = '';
  showHints.value = false;
  hintsUsed.value = 0;
};

// Navigation functions
const scrollToSection = (sectionId: string) => {
  const element = document.getElementById(sectionId);
  if (element) {
    element.scrollIntoView({ behavior: 'smooth' });
  }
};

// Track current section based on scroll position
const updateCurrentSection = () => {
  const sectionElements = document.querySelectorAll('section[id]');
  const scrollPosition = window.scrollY + 200; // Offset for header

  for (let i = sectionElements.length - 1; i >= 0; i--) {
    const section = sectionElements[i] as HTMLElement;
    if (section.offsetTop <= scrollPosition) {
      const sectionIndex = sections.value.findIndex((s: { id: string }) => s.id === section.id);
      if (sectionIndex !== -1) {
        currentSection.value = sectionIndex + 1;
      }
      break;
    }
  }
};

// Scroll event listener
const handleScroll = () => {
  updateCurrentSection();
};

onMounted(() => {
  // Initialize demo lesson
  console.log('Demo lesson loaded');
  console.log('aiExerciseService available:', !!aiExerciseService);
  
  // Test API connection
  testApiConnection();
  
  // Add scroll listener
  window.addEventListener('scroll', handleScroll);
  updateCurrentSection();
});

// Test API connection
const testApiConnection = async () => {
  try {
    console.log('Testing API connection...');
    const testHint = await aiExerciseService.generateHint(
      'Test',
      'Test description',
      'Test answer'
    );
    console.log('API test successful:', testHint);
  } catch (error) {
    console.error('API test failed:', error);
  }
};

onUnmounted(() => {
  // Remove scroll listener
  window.removeEventListener('scroll', handleScroll);
});
</script>
